.grid-container {
  display: grid;
  width: 100%;
  gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
}
  
  .grid-container article {
    padding: 15px;
    border-radius: 10px;
  }
  
  .grid-container img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .hero-dark {
    background-color: rgb(217, 232, 238);
  }
  
  .hero-light {
    background-color: rgb(181, 239, 218);
  }
  
  .weapon {
    font-weight: bold;
  }